import { Button, Card, CardContent, Container, Grid } from "@mui/material";
import React from "react";
import InsertInvitationIcon from "@mui/icons-material/InsertInvitation";
import ContactPageIcon from "@mui/icons-material/ContactPage";
import FileSaver from "file-saver";

const SaveTheDate = () => (
  <Container sx={{ textAlign: "center", my: 3 }} id="save-the-date">
    <Card
      variant="outlined"
      sx={{
        bgcolor: "primary.light",
        borderRadius: 2,
        px: { xs: 0, sm: 3 },
      }}
    >
      <CardContent>
        <h1>
          Save the date&nbsp;! 
          <br />
          Samedi 20 aot 2022 en&nbsp;Vende
        </h1>
        <p>
          Vous lavez compris, <strong> nous allons nous marier </strong> et
          nous comptons sur vous&nbsp;!
          <br />
          Un faire-part vous sera adress prochainement.
          <br />
          Nous mettrons ce site  jour au fur et  mesure de lavance des
          prparatifs.
        </p>
      </CardContent>
    </Card>
    <Grid container gap={2}>
      <Grid item sm>
        <h2>1. Notez la date dans vos agendas </h2>
        <p>
          20 aot 2022, 20 aot 2022, 20 aot 2022.
          <br />
          C'est bon vous avez retenu ? Dans le doute,  vos agendas&nbsp;!
          <br />
          Si vous tes super high-tech ou aventuriers, vous pouvez vous risquer
           cliquer sur le bouton et ouvrir l'vnement avec votre calendrier en
          ligne (Google, Outlook, iCloud, etc.). 
        </p>
        <Button
          variant="contained"
          size="large"
          startIcon={<InsertInvitationIcon />}
          onClick={() => {
            FileSaver.saveAs(
              "./mariage-thibetclaire.ics",
              "mariage-thibetclaire.ics"
            );
          }}
        >
          AJOUTER AU CALENDRIER
        </Button>
      </Grid>
      <Grid item sm>
        <h2>2. Envoyez-nous vos coordonnes </h2>
        <p>
          Grce  un formulaire simple et scuris, vous allez nous aider
          grandement en prvision de l'envoi des faire-part et pour faciliter
          l'organisation&nbsp;! 
        </p>
        <Button
          variant="contained"
          size="large"
          startIcon={<ContactPageIcon />}
          onClick={() =>
            window.open(
              "https://framaforms.org/mariage-de-claire-et-thibault-1645703062",
              "_self"
            )
          }
        >
          ENVOYER SES COORDONNES
        </Button>
      </Grid>
    </Grid>
  </Container>
);

export default SaveTheDate;
